#Detail{height:calc(100vh - 60px);display:grid;grid-template-columns:repeat(2, 1fr)}#Detail>div.left{position:relative;background-color:#f8f8f8;display:grid;place-items:center;overflow:hidden}#Detail>div.left .fixer{position:absolute;left:0;top:0;width:fit-content;height:100%;display:flex}#Detail>div.left .fixer .slide{position:relative;width:50vw;height:100%;overflow:hidden}#Detail>div.left .fixer .slide img{position:absolute;bottom:0;width:100%}#Detail>div.left .wrap_button{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);padding:1px;width:calc(100% + 4px);display:flex;justify-content:space-between}#Detail>div.left .wrap_button button{width:60px;aspect-ratio:1;background-color:#fff;border:1px solid #000}#Detail>div.right{padding:100px 160px 100px 100px;border-left:1px solid #000;display:flex;flex-direction:column;gap:40px}#Detail>div.right .wrap_price{margin-top:-36px;display:flex;align-items:center;gap:10px}#Detail>div.right .wrap_price .price{padding-top:3px;margin-left:auto;color:#bbb}#Detail>div.right .wrap_description{display:flex;flex-direction:column;gap:10px}#Detail>div.right .wrap_description .note{margin-top:30px}#Detail>div.right .wrap_info{display:grid}#Detail>div.right .wrap_info>div{position:relative;padding:15px;border-top:1px solid #000}#Detail>div.right .wrap_info>div::after{position:absolute;right:15px;top:50%;transform:translate(0, -50%);content:"+";font-size:20px}#Detail>div.right .wrap_button{margin-top:auto;width:100%;display:grid;grid-template-columns:repeat(2, 1fr);gap:1px}#Detail>div.right .wrap_button button{padding:15px;color:#fff;background-color:#000}#Detail>div.right .description,#Detail>div.right .note,#Detail>div.right .fragrance,#Detail>div.right .volume,#Detail>div.right .price,#Detail>div.right .wrap_button,#Detail>div.right .wrap_info{font-size:16px}#Detail>div.right .wrap_title h4,#Detail>div.right .volume,#Detail>div.right .note,#Detail>div.right .description{font-weight:300}#Detail>div.right .dc{font-weight:500}#Detail>div.modal{z-index:999;position:fixed;left:0;top:0;width:100%;height:100vh;background-color:rgba(0,0,0,.3137254902);display:grid;place-items:center}#Detail>div.modal .content{width:480px;height:360px;background-color:#fff;display:grid;grid-template-rows:310px 50px}#Detail>div.modal .content .top{position:relative;background:url(https://buly.kr/611kTKN) center 65%/140px no-repeat}#Detail>div.modal .content .top::after{content:"장바구니에 담았습니다";position:absolute;left:50%;top:60px;transform:translate(-50%, -50%);font-size:20px;font-weight:500}#Detail>div.modal .content .bot{display:flex}#Detail>div.modal .content .bot a,#Detail>div.modal .content .bot button{position:relative;flex:1;color:#fff;background-color:#000;font-size:15px;display:grid;place-items:center;cursor:pointer}#Detail>div.modal .content .bot a::before{content:"";position:absolute;left:0;top:50%;width:1px;height:50%;transform:translate(0, -50%);background-color:#fff}